/***
Page Header
***/

.page-header.navbar {
  width: 100%;
  padding: 0 20px 0 20px;
  margin: 0;
  border: 0px;
  padding: 0px;
  box-shadow: none;
  height: $header-height;
  min-height: $header-height;
  filter: none;
  background-image: none;

  /* Fixed header */
  &.navbar-fixed-top {
	z-index: $zindex-header-fixed;
  }

  &.navbar-static-top {
	z-index: $zindex-header-static;
  }

  /* Header logo */
  .page-logo {
	float: left;
	display: block;
	width: $sidebar-logo-container-width;
	height: $header-height;
	padding-left: 20px;
	padding-right: 20px;

	.page-sidebar-closed.page-sidebar-closed-hide-logo & {
	  padding: 0;
	}

	> .logo-image,
	> a {
	  display: inline-block;
	  float: left;
	}

	.logo-default {
	  margin: $header-logo-margin;
	}

	.logo-mini {
	  display: none;
	  margin-left: 5px;
	}

	.text-logo {
	  padding-left: 20px;
	  padding-top: 12px;
	}
  }

  /* Search box */
  .search-form {
	display: inline-block;
	width: $header-height;
	position: relative;
	float: left;
	transition: all 0.6s;

	.input-group {
	  .form-control {
		height: $header-height;
		border: 0;
		background: transparent !important;
		font-size: 13px;
		padding-left: 0;
		margin-left: 12px;
		text-indent: -150000px;

		&:hover {
		  cursor: pointer;
		}
	  }

	  .input-group-btn {
		height: $header-height;

		.btn.submit {
		  margin-left: -24px;
		  padding:0;
		  width: $header-height;
		  background: none;
		  margin-top: 4px;
		  display: block;

		  > i {
			font-size: 15px;
		  }
		}
	  }
	}


	&.open {
	  transition: all 0.6s;
	  width: 300px !important;

	  .input-group {
		.form-control {
		  text-indent: 0;

		  &:hover {
			cursor:text;
		  }
		}

		.input-group-btn {
		  .btn.submit {
			margin-left: 0;
		  }
		}
	  }
	}
  }

  /* Menu Toggler */
  .menu-toggler {
	display: block;
	cursor: pointer;
	opacity: 0.7;
	filter: alpha(opacity=70);
	width: $header-menu-toggler-width;
	height: $header-menu-toggler-height;
	background-repeat: no-repeat;
	background-position: center center;

	&:hover {
	  filter: alpha(opacity=100);
	  opacity: 1;
	}

	&.sidebar-toggler {
	  float: right;
	  margin: $header-sidebar-toggler-margin;

	  .page-sidebar-closed.page-sidebar-closed-hide-logo & {
		margin-right: ($sidebar-collapsed-width - $header-menu-toggler-width ) / 2 ;
	  }
	}

	&.responsive-toggler {
	  display: none;
	  float: right;
	  margin: $header-responsive-toggler-margin;
	}
  }

  /* Top menu */
  .top-menu {
	margin: 0;
	padding: 0;
	float: right;

	.navbar-nav {
	  padding: 0;
	  margin-right: 20px;
	  display: block;

	  > li.dropdown {
		margin: 0px;
		padding: 0px 4px;
		height: $header-height;
		display: inline-block;

		&:last-child {
		  padding-right: 0px;
		}

		> .dropdown-toggle {
		  margin: 0px;
		  padding: $header-top-menu-general-item-padding;

		  &:last-child {
			padding-right: 0;
		  }

		  > i {
			font-size: $header-top-menu-icon-font-size;

			&.glyphicon {
			  font-size: $header-top-menu-icon-font-size - 1;
			}
		  }

		  > .badge {
			font-family: $font-family-primary;
			position: absolute;
			top: $header-top-menu-general-item-badge-top;
			right: 20px;
			font-weight: 300px;
			padding: 3px 6px;
		  }

		  &:focus {
			background: none;
		  }
		}

		.dropdown-menu {
		  margin-top: 3px;

		  @include border-radius(4px);

		  &:before {
			position: absolute;
			top: -7px;
			right: 9px;
			display: inline-block !important;
			border-right: 7px solid transparent;
			border-bottom: 7px solid #eee;
			border-left: 7px solid transparent;
			border-bottom-color: rgba(0, 0, 0, 0.2);
			content: '';
		  }

		  &:after {
			position: absolute;
			top: -6px;
			right: 10px;
			display: inline-block !important;
			border-right: 6px solid transparent;
			border-bottom: 6px solid #fff;
			border-left: 6px solid transparent;
			content: '';
		  }

		  > li {
			> a {
			  color: #555;

			}
		  }
		}
	  }

	  /* Extended Dropdowns */
	  > li.dropdown-extended {

		.dropdown-menu {
		  min-width: 160px;
		  max-width: 275px;
		  width: 275px;
		  z-index: $zindex-header-fixed;

		  > li.external {
			display: block;
			overflow: hidden;
			padding: 15px 15px;
			letter-spacing: 0.5px;

			@include border-radius(4px 4px 0 0);

			> h3 {
			  margin: 0;
			  padding: 0;
			  float: left;
			  font-size: 13px;
			  display: inline-block;
			}

			> a {
			  display: inline-block;
			  padding: 0;
			  background: none;
			  clear: inherit;
			  font-size: 13px;
			  font-weight: 300;
			  position: absolute;
			  right: 10px;
			  border: 0;
			  margin-top: -1px;

			  &:hover {
				text-decoration: underline;
			  }
			}
		  }

		  /* header notifications dropdowns */
		  .dropdown-menu-list {
			padding-right: 0 !important;
			padding-left: 0;
			list-style: none;

			> li {
			  > a {
				display: block;
				clear: both;
				font-weight: 300;
				line-height: 20px;
				white-space: normal;
				font-size: 13px;
				padding: 16px 15px 18px;
				text-shadow: none;

				&:hover {
				  @include opacity(1);
				}
			  }

			  &:first-child a {
				border-top: none;
			  }
			}
		  }
		}
	  }

	  /* Notification */
	  > li.dropdown-notification {
		.dropdown-menu {
		  .dropdown-menu-list {
			> li {
			  a {
				.details {
				  overflow:hidden;

				  .label-icon {
					margin-right: 10px;
					@include border-radius(50%);

					i {
					  margin-right: 2px;
					  margin-left: 1px;
					}

					.badge {
					  right: 15px;
					}
				  }
				}

				.time {
				  float:right;
				  max-width:75px;

				  font-size: 11px;
				  font-weight: 400;
				  @include opacity(0.7);

				  text-align: right;
				  padding: 1px 5px;
				}
			  }
			}
		  }
		}
	  }

	  /* Inbox */
	  > li.dropdown-inbox {

		> .dropdown-menu {
		  .dropdown-menu-list {
			> li {
			  .photo {
				float: left;
				margin: 0 6px 6px 0;

				img {
				  height: 40px;
				  width: 40px;
				  @include border-radius(50% !important);
				}
			  }

			  .subject {
				display: block;
				margin-left: 46px;

				.from {
				  font-size: 13px;
				  font-weight: 600;
				}

				.time {
				  font-size: 12px;
				  font-weight: 400;
				  @include opacity(0.5);
				  float: right;
				}
			  }

			  .message {
				display: block !important;
				font-size: 12px;
				line-height: 1.3;
				margin-left: 46px;
			  }
			}
		  }
		}
	  }

	  /* Tasks */
	  > li.dropdown-tasks {
		.dropdown-menu {
		  .dropdown-menu-list {
			> li {
			  .task {
				margin-bottom: 5px;

				.desc {
				  font-size: 13px;
				  font-weight: 300;
				}

				.percent {
				  float: right;
				  font-weight: 600;
				  display: inline-block;
				}
			  }

			  .progress {
				display: block;
				height: 8px;
				margin: 8px 0 2px;

				.progress-bar {
				  box-shadow: none;
				}
			  }
			}
		  }
		}
	  }

	  /* User */
	  > li.dropdown-user {
		.dropdown-toggle {
		  padding: $header-top-menu-user-item-padding;

		  > .username {
			display: inline-block;
			font-size: $header-top-menu-user-font-size;
			font-weight: $header-top-menu-user-font-weight;
		  }

		  > img {
			float: left;
			margin-top: -5px;
			margin-right: 5px;
			height: 29px;
			display: inline-block;
		  }

		  > i {
			display: inline-block;
			margin-top: 5px;
			margin: 0;
			font-size: $header-top-menu-user-font-size;
		  }
		}

		.dropdown-menu {
		  width: 175px;
		  > li {
			> a {
			  font-size: $header-top-menu-user-dropdown-link-font-size;
			  font-weight: $header-top-menu-user-dropdown-link-font-weight;

			  i {
				width: 15px;
				display: inline-block;
				margin-right: 9px;
			  }

			  .badge {
				margin-right: 10px;
			  }
			}
		  }
		}
	  }

	  /* Language */
	  > li.dropdown-language {
		padding-left: 0;
		padding-right: 0;
		margin: 0;

		> .dropdown-toggle {
		  padding: $header-top-menu-language-item-padding;

		  > img {
			margin-bottom: 2px;
		  }

		  > i {
			font-size: 14px;
		  }
		}

		> .dropdown-menu {
		  > li {
			> a {
			  font-size: 13px;

			  > img {
				margin-bottom: 2px;
				margin-right: 5px;
			  }
			}
		  }
		}
	  }

	  /* Dark version */
	  li.dropdown-dark {
		.dropdown-menu {
		  &:before {
			border-left: none;
			border-right: none;
		  }

		  .dropdown-menu-list {
			> li.external {

			  a {
				background: none !important;
				border: none !important;
			  }
			}
		  }
		}
	  }
	}
  }
}

/* Allow expanded search for above 768px */
@media (min-width: $screen-sm-min) {  /* 768px */
	.page-header.navbar {

		/* Search box */ 
		.search-form {
			&.search-form-expanded {
				width: 200px;

				.input-group {
					.form-control {
						text-indent: 0;

						&:hover {
							cursor:text;
						}
					}

					.input-group-btn {
						.btn.submit {
							margin-left: 0;
						}
					}
				}
			}
		}
	}
}

/*** 
Horizontal Menu 
***/

.page-header.navbar {

	/* Header container */
	.container {
		position: relative;
	}

	/* Mega menu */
        .hor-menu {
            margin: 0 0 0 -17px;
            margin: 0;
            float: left;

            .navbar-nav {
                position: static;

                &.navbar-right {
                    .dropdown-menu {
                        left: auto;
                        right: 0;
                    }
                }

                /* Mega menu */
                > li.mega-menu-dropdown {
                    position: static;

                    > .dropdown-menu {
                        left: auto;
                        width: auto;   

                        .mega-menu-content {
                            font-family: $font-family-primary;  
                            padding: 15px;
                            margin: 0;

                            &.mega-menu-responsive-content {
                                padding: 10px 18px 10px 45px;
                            }

                            .mega-menu-submenu {
                                padding: 0;
                                margin: 0;

                                &:last-child {
                                    border-right: 0;
                                }

                                li {
                                    padding: 1px !important;
                                    margin: 0 !important;
                                    list-style: none;

                                    > h3 {
                                        margin-top: 5px;
                                        padding-left: 6px;                  
                                        font-size: $header-hor-menu-mega-menu-header-font-size;
                                        font-weight: $header-hor-menu-mega-menu-header-font-weight;
                                    }

                                    > a { 
                                        white-space: normal;
                                        font-family: $font-family-primary;
                                        padding: 7px;  
                                        margin: 0;                 
                                        font-size:  $header-hor-menu-dropdown-menu-link-font-size;
                                        font-weight:  $header-hor-menu-dropdown-menu-link-font-weight;

                                        &.iconify {
                                            padding: 7px 7px 7px 30px; 

                                            > i {
                                                position: absolute;
                                                top: auto !important;
                                                margin-left: -24px;
                                                font-size: 15px;
                                                margin-top: 3px !important;
                                            }
                                        }

                                        .badge,
                                        .label {
                                            margin-left: 5px; 
                                        }
                                    }
                                }
                            }
                        }
                    }

                    &.mega-menu-full {
                        .dropdown-menu {
                            left: 20px;
                            right: 20px;
                        }
                    }
                }

                /* Mega Menu Dropdown */
                > li.menu-dropdown {
                    .dropdown-menu {
                        &:after,
                        &:before {
                            display: none !important;
                        }
                    }
                }

                /* Classic menu */
                > li {
                    > a {         
                        font-size: $header-hor-menu-default-link-font-size;
                        font-weight: $header-hor-menu-default-link-font-weight;
                        padding: $header-hor-menu-general-item-padding;  

                        &:focus {
                            background: none !important;
                        }        
                    }

                    &.current,
                    &.active {
                        .selected {
                            left: 50%;
                            bottom:0;
                            position: absolute;
                            border-left: 6px solid transparent;
                            border-right: 6px solid transparent;
                            border-top: 6px solid transparent;
                            display: inline-block;
                            margin: 0;
                            width: 0;
                            height:0px;
                            margin-left: -7px;
                            margin-bottom:-6px;  
                        }
                    }

                    .dropdown-menu {
                        margin-top: 0;
                        border: none;

                        li {
                            > a {
                                font-family: $font-family-primary;
                                font-size:  $header-hor-menu-dropdown-menu-link-font-size;
                                font-weight:  $header-hor-menu-dropdown-menu-link-font-weight;
                                padding: $header-hor-menu-submenu-item-padding;
                                white-space: normal;

                                .label,
                                .badge {
                                    font-weight: 300;  
                                }
                            }
                        }
                    }



                    &.classic-menu-dropdown {
                        .dropdown-menu {
                            min-width: 195px;
                            max-width: 235px;
                        }  
                    }

                    .dropdown-submenu {
                        > .dropdown-menu {
                            top: 0;
                        }

                        > a:after {
                            top: $header-hor-menu-submenu-item-arrow-top;
                            right: 10px;
                        }
                    }     
                }
            }
        }
}

/* Form medium devices upto large devices */

@media (min-width: $screen-md-min) and (max-width: $screen-lg-min) {  /* 992px 1200px */
	/* Boxed layout */
	.page-boxed {
		.page-header.navbar {  
			/* Top menu */  
			.top-menu {
				.navbar-nav {
					> li.dropdown-user {
						.dropdown-toggle {
							.username.username-hide-on-mobile {
								display: none;
							}
						}
					}

					> li.dropdown-language {
						.dropdown-toggle {
							.langname {
								display: none;
							}
						}
					}
				}
			}
		}
	}	
}

@media (min-width: $screen-md-min) { /* 992px */

	/* Page header */
	.page-header.navbar {
		/* Header logo */
		.page-logo {
			.page-sidebar-closed.page-sidebar-closed-hide-logo & {
				padding: 0;
			}

			.page-sidebar-closed.page-sidebar-closed-hide-logo & { 
				width: $sidebar-collapsed-width;

				.logo-default {
					display: none;
				}
			}
		}
	}

	/* Boxed Layout */
	.page-boxed	{
		.page-header.navbar {
			/* Page logo */
			.page-logo {
				width: ($sidebar-width + 1px);
			}

			/* Top menu */
			.top-menu {
				.navbar-nav {
					margin-right: 0px;
				}	
			}
		}
	}

	/* Sidebar closed & logo hidden */
	.page-sidebar-closed.page-sidebar-closed-hide-logo.page-boxed {
		.page-header.navbar {
			/* Page logo */
			.page-logo {
				width: ($sidebar-collapsed-width + 1);
			}
		}
	}

	/* Boxed layout & page sidebar fixed layout */
	.page-boxed.page-sidebar-fixed {
		.page-header.navbar {
			/* Page logo */
			.page-logo {
				width: $sidebar-width;
			}
		}
	}
}

@media (max-width: $screen-sm-max) { /* 991px */

	/* Page header */
	.page-header.navbar {
		padding: 0 20px 0 20px;
		position: relative;
		clear: both;

		/* Page logo */
		.page-logo {
			width: auto;
			padding: 0;
			margin-right: 10px;
			margin-left: 0px !important;
			padding-left: 0px !important; 

			img {
				margin-left: 4px !important;
			}
		}

		/* Menu Toggler */
		.menu-toggler {
			&.sidebar-toggler {
				display:none !important;
			}

			&.responsive-toggler {
				display: inline-block;
			}
		}

		/* Top Menu */
		.top-menu {
			.navbar-nav {
				display: inline-block;
				margin: 0 10px 0 0;

				> li {
					float: left;
				}

				.nav li.dropdown i {
					display: inline-block;
					position: relative;
					top:1px;
					right:0px;
				}

				.open .dropdown-menu  {
					position: absolute;
				}
			}
		}	
	}

	/* Fixed header for mobile */
	.page-header-fixed.page-header-fixed-mobile {
		.navbar-fixed-top {
			position: fixed;
		}
	}	 

	/* Boxed Layout */
	.page-boxed .page-header.navbar {
		> .container {
			max-width: none !important;
			margin: 0 !important; 
			padding: 0 !important;
		}
	}
}

@media (min-width: $screen-sm-min) and (max-width: $screen-sm-max) { /* 768px & 991px */

	/* Boxed Layout */
	.page-boxed {
		.page-header.navbar  {
			margin: auto !important;
			padding: 0;

			> .container {
				margin: auto !important;
			}
		}
	}
}

@media (max-width: $screen-xs-max) { /* 767px */

	/* Page header */	
	.page-header.navbar {
		padding: 0 10px 0 10px;

		/* Header logo */
		.page-logo {
			width: auto;
		}

		/* Search box */ 
		.search-form {
			&.open {
				z-index: 3;
				left: 10px;
				right: 10px;
				position: absolute;
				width: auto !important;
			}
		}

		/* Top navigation menu*/
		.top-menu {
			.navbar-nav {

                > li.dropdown-extended {
                    > .dropdown-menu {
                        max-width: 255px;
                        width: 255px;
                    }
                }

				> li.dropdown-notification {
					.dropdown-menu {
						margin-right: -190px;

						&:after,
						&:before {
							margin-right: 190px;   
						}
					}
				}

				> li.dropdown-inbox {
					.dropdown-menu {
						margin-right: -150px;

						&:after,
						&:before {
							margin-right: 150px;
						}
					}
				}

				> li.dropdown-tasks {
					.dropdown-menu {
						margin-right: -110px;

						&:after,
						&:before {
							margin-right: 110px;
						}
					}
				}
			}
		}
	}
}

@media (max-width: 580px) {

	/* Page header */
	.page-header.navbar {
		.top-menu {
			.navbar-nav {
				> li.dropdown-user {
					.dropdown-toggle {
						.username.username-hide-on-mobile {
							display: none;
						}
					}
				}

				> li.dropdown-language {
					.dropdown-toggle {
						.langname {
							display: none;
						}
					}
				}
			}
		}
	}
}

@media (max-width: $screen-xs-min) { /* 480px */

	/* Fixed header for mobile */
	.page-header-fixed.page-header-fixed-mobile {
		.page-header.navbar {
			height: $header-height-xs;
		}
	}

	.page-header.navbar {
		/* Top menu */
		.top-menu {
			display: block;
			clear: both;

			.navbar-nav {				
				margin-right: 0;

				> li.dropdown {
					.dropdown-toggle {
						padding: $header-top-menu-general-item-padding-on-mobile;
					}
				}

				> li.dropdown-language {
					.dropdown-toggle {
						padding: $header-top-menu-language-item-padding-on-mobile;
					}
				}

				> li.dropdown-user {
					.dropdown-toggle {
						padding: $header-top-menu-user-item-padding-on-mobile;
					}
				}
			}
		}
	}
}

/***
Pace - Page Progress
***/

.pace .pace-progress {
  z-index: 10000;
  top: $header-height - 2px;
  height: 2px;
}

.pace .pace-progress-inner {
  box-shadow: none;
}

.pace .pace-activity {
  top: $header-height + 2px;
  right: 22px;
  border-radius: 10px !important; 
}

@media (max-width: $screen-xs-min) { /* 480px */ 

  .page-header-fixed .pace .pace-progress {
    top: ($header-height * 2);
  }

  .page-header-fixed .pace .pace-activity {
    top: ($header-height * 4) + 4px;
    right: 15px;
  }

}